.nav{
    background: #fbfbfb;
    height: 100vh;
    padding: 1rem 0;
}
.profile{
    display: flex;
    flex-direction: row;
    align-items: center;
    background: #ffff;
    margin-bottom: 1rem;
    padding: 0.85rem 0.25rem;
    text-align: center;
    border-radius: 8px;
}
.profile-name{
    font-size: 1.25rem;
    font-weight: bold;
}
.profile img{
    display: inline-block;
    width: 30px;
    margin: 0 0.85rem;
    border-radius: 5px;
}
button{
    width: 100%;
    background: #fbfbfb;
    outline: 0;
    border: none; /* 1px solid #fbfbfb */
    font-size: 1rem;
    margin: 0.85rem 0;
    text-align: left;
    border-radius: 8px;
    padding: 0.5rem 0.25rem;
}
button:focus{
    background: white;
}
.container{
    padding: 0 0.6rem;
}
.navbar{
    display: flex;
    flex-direction: column;
    margin-bottom: 5rem;
}