.notes{
    color: #4a4a4a;
    padding-top: 6rem;
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(4, 2fr);
}
.note-preview{
  box-shadow: 1px 1px 5px #cdcdcd;;
    overflow: hidden;
    padding: 1rem 0.85rem;
}
.note-title{
    color: black;
    font-size: 1.25rem;
}
.note-body{
    font-size: 1rem;
    word-wrap: break-word;
    padding-bottom: 0.25rem;
}
.note-date{
    color: #787676;
    font-size: 0.7rem;
}