@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap');

*, *::before, *::after{
   box-sizing: border-box;
  }
  
*{
   margin: 0;
 }
::-webkit-scrollbar{
  display: none;
}
body{
	font-family:'Nunito', sans-serif;
}
a{
  text-decoration: none;
}
.home{
	background: #ffffff;
  display: grid;
  grid-template-columns: [first] 20fr [line2] 80fr [end];
  gap: 0.85rem;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
.main{
  margin: 1rem 0;
  overflow-y: scroll;
  /* margin-right: 0.85rem; */
}
.searchbar{
  position: fixed;
  width: 100%;
  outline: 0;
  border: none;
  box-shadow: 0 2px 3px #f5f2f2;
  padding: 0.5rem 0.25rem;
  border-radius: 5px;
  font-size: 1rem;
}
.add-note{
  position: fixed;
  top: 65px;
  right: 100px;
  color: black;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}